//  为谁投保

<template>
  <div class="top">
    <h4>请选择投保的对象</h4>

    <div class="radio">
      <div class="self">
        <div @click="clickSelf">
          <img src="@/assets/img/为谁投保/男生.png" alt="" />
          <img v-if="radio == 1" src="@/assets/img/为谁投保/选中.png" alt="" />
          <img
            v-if="radio == 2"
            src="@/assets/img/为谁投保/未选中.png"
            alt=""
          />
          <h3>本人</h3>
        </div>
      </div>
      <div class="child">
        <div @click="clickChild">
          <img src="@/assets/img/为谁投保/女生.png" alt="" />
          <img v-if="radio == 2" src="@/assets/img/为谁投保/选中.png" alt="" />
          <img
            v-if="radio == 1"
            src="@/assets/img/为谁投保/未选中.png"
            alt=""
          />
          <h3>子女</h3>
        </div>
      </div>
    </div>

    <div>
      <van-button class="but" type="info" @click="nextStep">下一步</van-button>
    </div>
  </div>
</template>

<script>
export default {
  name: "",
  components: {},

  data() {
    return {
      radio: "1",
      activeIcon: "https://img01.yzcdn.cn/vant/user-active.png",
      inactiveIcon: "https://img01.yzcdn.cn/vant/user-inactive.png",
    };
  },

  created() {},
  mounted() {},
  methods: {
    clickSelf() {
      this.radio = 1;
    },
    clickChild() {
      this.radio = 2;
    },
    nextStep() {
      this.$router.push({
        path: "/informationEntry",
        query: {
          radio: this.radio,
        },
      });
    },
  },
  watch: {},
  computed: {},
};
</script>

<style lang='scss' scoped>
.top {
  width: 100%;
  display: flex;
  flex-direction: column;
}
h4 {
  margin-top: 20vh;
  text-indent: 1cm;
}
.img-icon {
  height: 20px;
}
.radio {
  margin: 15vh 10vh 0 10vh;
  display: flex;
  justify-content: space-between;
}

.but {
  width: 100%;
  border-radius: 18px;
  position: fixed;
  bottom: 10px;
}

.van-radio {
  height: 50px;
}

.self {
  div {
  }
}

.child {
}

h3{
  margin-left: 3vw;
  margin-top: 5vw;
}
</style>
